@import "./reset.css";

* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  margin: 80px 0px;
}

body,
#potato,
.container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  font-weight: 600;
}

.movies {
  width: 80%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 35vh;
  gap: 80px;
}

.movie {
  width: 100%;
  height: 100%;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  display: flex;
  align-items: center;
  padding: 0px 20px;
  font-size: 18px;
  color: rgba(0, 0, 0, 0.7);
}

.movie img {
  max-width: 150px;
  margin-right: 20px;
  position: relative;
  top: -50px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.5);
}

.movie .movie__data {
  width: 100%;
  height: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.movie__title {
  font-size: 25px;
  font-weight: 600;
}

.genres {
  display: flex;
}

.genres__genre:not(:last-child) {
  margin-right: 10px;
}

.movie__summary {
  line-height: 1.5;
}
